*, :before, :after {
  box-sizing: border-box;
}

img {
  display: block;
}

h4 {
  text-shadow: 1.5px 1.5px 6px #000;
  font-size: 30px;
}

.loader {
  display: flex;
  transform: translateZ(1px);
}

.loader:after {
  content: "";
  width: 48px;
  height: 48px;
  background: #f8b409;
  border-radius: 50%;
  margin: 8px;
  animation: coin-flip 2.4s cubic-bezier(0, .2, .8, 1) infinite;
  display: inline-block;
}

@keyframes coin-flip {
  0%, 100% {
    animation-timing-function: cubic-bezier(.5, 0, 1, .5);
  }

  0% {
    transform: rotateY(0deg);
  }

  50% {
    animation-timing-function: cubic-bezier(0, .5, .5, 1);
    transform: rotateY(1800deg);
  }

  100% {
    transform: rotateY(3600deg);
  }
}

.form {
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.cat-info {
  align-items: flex-start;
  gap: 5px;
  display: flex;
}

.breed-select {
  width: fit-content;
  padding: 10px;
}

.image-container {
  width: 30%;
  height: 30%;
}

.image-container img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.cat-temp-span {
  font-size: 20px;
  font-weight: 900;
}

.cat-body {
  width: 30%;
}

.is-hidden {
  display: none;
}

